import React, { FC, useState } from 'react';
import { Link } from 'gatsby';
import { Collapse } from 'react-collapse';

import { NavItemProps } from './NavItemProps';

export const DesktopNavItem: FC<NavItemProps> = ({
  title,
  basePath,
  items,
}) => {
  const [isOpen, setIsOpen] = useState<boolean>(false);

  return (
    <div
      className={'relative text-jet-black'}
      onMouseEnter={() => setIsOpen(true)}
      onMouseLeave={() => setIsOpen(false)}
    >
      <Link to={basePath} style={{ textDecoration: 'none', color: 'inherit' }}>
        <div
          className={
            'border-b-2 border-transparent hover:border-emerald-green py1 text-base md:text-sm lg:text-lg font-bold'
          }
        >
          <p className={'text-center'}>{title}</p>
        </div>
      </Link>
      {items && (
        <div
          className={'absolute w-full bg-gray-100 overflow-hidden rounded-b'}
          style={{ minWidth: '175px' }}
        >
          <Collapse isOpened={isOpen}>
            {items.map(({ path, child }, index) => (
              <div key={index} className={'hover:bg-gray-200'}>
                <Link
                  to={`${basePath}${path}`}
                  style={{
                    display: 'inline-block',
                    textDecoration: 'none',
                    color: 'inherit',
                    width: '100%',
                    padding: '1rem',
                  }}
                  onClick={() => setIsOpen(false)}
                >
                  {child}
                </Link>
              </div>
            ))}
          </Collapse>
        </div>
      )}
    </div>
  );
};
