import React, { CSSProperties, FC } from 'react';

interface InnerImageProps {
  src: string;
  alt: string;
  style?: CSSProperties;
}

export const InnerImage: FC<InnerImageProps> = ({ src, alt, style }) => (
  <img
    src={src}
    alt={alt}
    className={'absolute inset-0 h-full w-full object-cover'}
    style={{
      ...style,
    }}
  />
);
